body.dark-mode .tech-icons {
  box-shadow: 0 6px 14px rgba(255, 188, 72, 0.25) !important;
}

body.dark-mode .express svg,
body.dark-mode .github svg {
  color: #ffffff;
}

.tech-icons {
  font-size: 4.5em !important;
  margin: 15px !important;
  padding: 10px !important;
  opacity: 0.93 !important;
  /* border: 1.7px solid #c989e6a2 !important; */
  border: 1.7px solid #ffbc48ed;
  vertical-align: middle !important;
  text-align: center !important;
  border-radius: 5px !important;
  display: table !important;
  box-shadow: 4px 5px 4px 3px #231703 !important;
  overflow: hidden !important;
  transition: all 0.4s ease 0s !important;
}

@media (max-width: 767px) {
  .tech-icons {
    margin: 10px !important;
  }
}

.tech-icons:hover {
  transform: scale(1.05) !important;
  overflow: hidden !important;
  border: 2.2px solid #d8a958fc !important;
}
